import { BackTop, Button, Col, Row } from 'antd';
import React, { Component } from 'react';
import { DragDropContext } from 'react-beautiful-dnd';
import PanelGroup from 'react-panelgroup';
import { connect } from 'react-redux';
import { clearItineraryInView } from '../../store/actions/viewActions';
import PageLayout from '../Core/PageLayout';
import ResultList from '../Results/ResultList';
import ResizeContainer from '../Utilities/ResizeContainer';
import { addToPublicIndex, getPublicQueryResults, loadMorePublicQueryResults } from './../../store/actions/queryActions';
import ItineraryViewer from './../ItineraryViewer';
import { content, panel } from './Discover.module.scss';

class Discover extends Component {

    onDragEnd = () => { }
    render() {

        console.log(this.props.currentQuery)
        return (
            <PageLayout>
                <BackTop />
                {this.props.itineraryInFocus &&
                    <div className={content}>
                        <PanelGroup borderColor="#e8e8e8"  >
                            <div className={content} style={{ width: "100%" }}>
                                {/* <div style={{ position: "absolute", top: 0, width: "100%", zIndex: 1001 }}>
                                    <FilterMenu drawer />
                                </div> */}
                                <div style={{ paddingTop: "70px", paddingBottom: "70px" }}>
                                    <Row type="flex" justify="center">
                                        <Col xs={22} sm={22} md={22} lg={20} xl={20} xxl={20}>
                                            <ResizeContainer>
                                                {this.props.currentQuery && <h2>Results for '{this.props.currentQuery}'</h2>}
                                                <ResultList></ResultList>
                                            </ResizeContainer>
                                        </Col>
                                    </Row>
                                    {this.props.nextQuery &&
                                        <Row type="flex" justify="center" style={{ paddingTop: "50px" }}>
                                            <Col>
                                                <Button onClick={() => this.props.loadMorePublicQueryResults()} type="primary" size="lg"> Load More </Button>
                                            </Col>
                                        </Row>
                                    }
                                </div>
                            </div>
                            <div className={content}>
                                <DragDropContext onDragEnd={this.onDragEnd} >
                                    <ItineraryViewer dragging={this.props.dragging} match={this.props.match} onBack={() => this.props.clearItineraryInView()}></ItineraryViewer>
                                </DragDropContext>
                            </div>
                        </PanelGroup>
                    </div>
                }

                {!this.props.itineraryInFocus &&
                    <div className={panel} style={{ width: "100%" }}>
                        <div className={content}>
                            {/* <div style={{ position: "fixed", top: 65, width: "100%", zIndex: 1001 }}>
                                <FilterMenu drawer />
                            </div> */}
                            <div style={{ paddingTop: "70px", paddingBottom: "70px" }}>
                                <Row type="flex" justify="center">
                                    <Col xs={22} sm={22} md={22} lg={20} xl={20} xxl={20}>
                                        <ResizeContainer>
                                            {this.props.currentQuery && <h2>Results for '{this.props.currentQuery}'</h2>}
                                            <ResultList></ResultList>
                                        </ResizeContainer>
                                    </Col>
                                </Row>
                                {
                                    this.props.nextQuery &&
                                    <Row type="flex" justify="center" style={{ paddingTop: "50px" }}>
                                        <Col>
                                            <Button onClick={() => this.props.loadMorePublicQueryResults()} type="primary" size="lg"> Load More </Button>
                                        </Col>
                                    </Row>
                                }
                            </div>
                        </div>
                    </div>
                }
            </PageLayout>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addToPublicIndex: (newItin) => dispatch(addToPublicIndex(newItin)),
        getPublicQueryResults: (coordinates) => dispatch(getPublicQueryResults(coordinates)),
        loadMorePublicQueryResults: () => dispatch(loadMorePublicQueryResults()),
        clearItineraryInView: () => dispatch(clearItineraryInView())
    }
}

const mapStateToProps = (state) => ({
    authError: state.auth.authError,
    loading: state.query.loading,
    nextQuery: state.query.nextQuery,
    modalOpen: state.query.discoverModalOpen,
    itineraryInFocus: state.view.itinerary,
    currentQuery: state.query.currentQuery
})

export default connect(mapStateToProps, mapDispatchToProps)(Discover);