import { Avatar, Card, Carousel, Dropdown, Icon, Menu, Rate, Row, Tag } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { querySpecificPublicDocument } from '../../store/actions/queryActions';
import { loadItineraryToState } from '../../store/actions/viewActions'
import { defaultAvatarImage, defaultCoverImage } from '../Core/Globals';
import { cardContainer, carousel, img, item, noReviews, ratingContainer, tagStyle } from './QueryCard.module.scss';
const { Meta } = Card;

class QueryCard extends Component {

    render() {
        let image = this.props.itin.img ? this.props.itin.img : defaultCoverImage

        const menu = (
            <Menu>
                <Menu.Item>
                    <Icon type="alert" />Report Itinerary
                </Menu.Item>
            </Menu>
        );

        let tags = this.props.itin.tags ? this.props.itin.tags.map((tag) => {
            return <Tag className={tagStyle} color="#1890ff">{tag}</Tag>
        }) : [];

        let drop = <Dropdown overlay={menu} placement="bottomLeft"><Icon type="ellipsis" /></Dropdown>

        let locationString = this.props.itin.location.map((location, index) => {
            return <span key={index}>{index + 1 !== this.props.itin.location.length ? location.name + " | " : location.name}</span>
        })

        let cover = <Carousel className={carousel} effect={"fade"} afterChange={this.onChange}>
            {
                this.props.itin && this.props.itin.meta.images && this.props.itin.meta.images.map((photo) => {
                    return (
                        <div><img className={img} alt="example" src={photo} /></div>
                    )
                })
            }
        </Carousel>


        return (
            <div className={item}>
                <Card
                    onClick={() => this.props.loadItineraryToState(this.props.itin.id)}
                    style={{ marginTop: "1em" }}
                    hoverable
                    cover={this.props.itin.meta.images ? cover : null}
                    actions={[<Icon type="heart" />, <Icon type="edit" />, drop]}
                >
                    <Row className={ratingContainer}>
                        <Rate allowHalf disabled defaultValue={this.props.itin.rating ? parseFloat((Math.round(this.props.itin.rating * 2) / 2).toFixed(1)) : 0} style={{ fontSize: 15, }} />
                        {
                            (!this.props.itin.rating) && <span className={noReviews}>(No Reviews)</span>
                        }
                        {
                            (this.props.itin.rating && this.props.itin.ratingCount) && <span className={noReviews}>({this.props.itin.ratingCount})</span>
                        }
                    </Row>
                    <Row>
                        <Meta
                            avatar={<Avatar src={!this.props.itin.meta.user.picture ? defaultAvatarImage : this.props.itin.meta.user.picture} />}
                            title={this.props.itin.meta.name}
                            description={locationString}
                        />
                    </Row>
                    <Row className={cardContainer}>
                        {tags.slice(0, 6)}
                    </Row>
                </Card>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        loadItineraryToState: (itinID) => dispatch(loadItineraryToState(itinID)),
        querySpecificPublicDocument: (itinID) => dispatch(querySpecificPublicDocument(itinID))
    }
}

export default connect(null, mapDispatchToProps)(QueryCard)